    </div>
    <div class="navlist">
        <div class="ls" @click="gotoMenuContent(item)" v-for="(item,index) in getMenus" :key="index" :class="getNavItm.path == item.path?'ac':''">
            <span class="iconfont" :class="item.icon"></span>
            <span>{{item.name}}</span>
        </div>
    </div>
    <div class="githuburl">
        <div><a :href="githubUrl" target="_blank">{{githubUrl}}</a></div>
    </div>
</div>
</template>

<script>
import {
    mapGetters
} from 'vuex'
import store from "../../store"

export default {
    name: 'LeftNav',
    data: function () {
        return {
            githubUrl: store.state.githubUrl
        }
    },
    computed: {
        ...mapGetters([
            'getNavItm',
            'getMenus'
        ])
    },
    props: {
        gotoMenuContent: Function
    }
}
</script>

