    // },
    data: function () {
        return {

        }
    },
    created: function () {},
    // 在 `methods` 对象中定义方法
    methods: {
        gotoMenuContent: function (item) {
            this.$router.push(item.path)
            this.$store.dispatch('setStore', {
                navItem: item
            })
        }
    },
    mounted: function () {

    }
}
</script>

