import Vue from 'vue'
import Vuex from 'vuex'
import menus from './menus'//menus

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menus,
    githubUrl: "https://github.com/cycxy",
    navItem: menus[0]
  },
  mutations: {
    setStore(state, payload) {
      for (const plkey in payload) {
        state[plkey] = payload[plkey]
      }
      console.log(state)
    }
  },
  actions: {
    setStore(context, payload) {
      return new Promise((resolve) => {
        context.commit('setStore', payload)
        resolve()
      })

    }
  },
  getters: {
    getMenus: function (state) {
      return state.menus
    },
    getNavItm: function (state) {
      return state.navItem
    }
  }
})
